import { codemirror } from 'vue-codemirror'; // require styles

import 'codemirror/lib/codemirror.css'; // language

import 'codemirror/mode/python/python.js'; // theme css

import 'codemirror/theme/base16-light.css'; // require active-line.js

import 'codemirror/addon/selection/active-line.js';
import 'codemirror/addon/edit/closebrackets.js'; // keyMap

import 'codemirror/mode/clike/clike.js';
import 'codemirror/addon/edit/matchbrackets.js';
import 'codemirror/addon/comment/comment.js';
import 'codemirror/addon/dialog/dialog.js';
import 'codemirror/addon/dialog/dialog.css';
import 'codemirror/addon/search/searchcursor.js';
import 'codemirror/addon/search/search.js';
import 'codemirror/addon/hint/show-hint.js';
import 'codemirror/addon/lint/lint.js'; //socket

import io from 'socket.io-client/dist/socket.io.js';
var socket = io.connect('http://47.94.199.65:5001');
export default {
  name: "editor",
  components: {
    codemirror: codemirror
  },
  data: function data() {
    var code = 'import time\n' + 'for i in range(10):\n' + '  time.sleep(3)\n' + '  print(i)';
    return {
      code: code,
      response_info: '',
      cmOption: {
        autoCloseBrackets: true,
        tabSize: 4,
        styleActiveLine: true,
        lineNumbers: true,
        line: true,
        mode: 'text/x-python',
        theme: 'base16-light',
        matchBrackets: true,
        // 括号匹配
        lineWrapping: true,
        // 自动换行
        lint: true,
        hint: true //代码提示

      }
    };
  },
  created: function created() {
    var _this = this;

    socket.on('connect', function (msg) {}); // 监听事件 server服务端需写server_response  不是内置的

    socket.on('print_log', function (msg) {
      console.log(msg.data);
      _this.response_info = _this.response_info + msg.data;
    });
  },
  methods: {
    onCmCodeChange: function onCmCodeChange(newCode) {
      this.code = newCode;
    },
    submit_code: function submit_code() {
      var _this2 = this;

      socket.emit('run', {
        data: 1
      });
      this.$http.post('http://47.94.199.65:5001/submit', {
        code: this.code
      }).then(function (response) {
        if (response.data != '1') {
          _this2.response_info = response.data;
        }
      }).catch(function (error) {
        console.log(error);
        _this2.errored = true;
      }).finally(function () {
        return _this2.loading = false;
      });
    }
  }
};